<template>
    <div class="center">
        <h1>Test - {{ questionIndex + 1 }} of {{ questions.length }} [{{ score }}%]</h1>
        <img :src="answer.img" :class="status"/>
        <ul v-if="!isCorrect">
            <li v-for="answer in getAnswers" :key="answer" @click="checkAnswer(answer)">{{ answer  }}</li>
        </ul>
        <h3 v-if="showButton">{{ answer.name }} - {{ answer.native }}</h3>
        <button v-if="showButton" @click="nextQuestion">Next Question &gt;&gt;</button>
    </div>
</template>

<script>
import firebase from 'firebase';
var statusTypes = { correct: 'correct', wrong: 'wrong', none: '' }

export default {
    props: [ 'wordlistid' ],
    data() {
        return {
            answer: {},
            wrongAnswers: [],
            status: '',
            questionIndex: 0,
            questions: [],
            answered: 0,
            answeredCorrect: 0
        }
    },
    mounted() {
        window.console.log("id: " + this.wordlistid);

        var db = firebase.firestore();
        db.collection('wordlist').doc(this.wordlistid).get()
            .then(doc => {
                if (!doc.exists) {
                    window.console.log("No such document");
                } else {
                    window.console.log("Data:");
                    window.console.log(doc.data());

                    for (var item of doc.data().items) {
                        this.questions.push(item);
                    }
                    this.shuffle(this.questions);
                    this.newQuestion();
                }
            })
            .catch(err => {
                window.console.log('Error', err);
            });
    },
    computed: {
        getAnswers() {
            window.console.log("called get answers");
            if (this.wrongAnswers.length > 0) {
                return this.shuffle([
                    this.answer.name,
                    this.wrongAnswers[0].name,
                    this.wrongAnswers[1].name,
                    this.wrongAnswers[2].name]);
            } else {
                return [];
            }
        },
        isCorrect() {
            return this.status === statusTypes.correct;
        },
        showButton() {
            return this.isCorrect;
        },
        score() {
            return this.answered > 0 ? parseInt(this.answeredCorrect / this.answered * 100): 0;
        }
    },
    methods: {
        newQuestion() {
            // var answerIndex = parseInt(Math.random() * this.theme.items.length);
            this.answer = this.questions[this.questionIndex];

            var wrongIndices = [];
            for (var index = 0; index < 3; index++) {
                var randomIndex = -1;
                while (randomIndex == -1) {
                    randomIndex = parseInt(Math.random() * this.questions.length);
                    if (randomIndex == this.questionIndex) {
                        randomIndex = -1;
                    } else {
                        for (let wrongIndex of wrongIndices) {
                            if (randomIndex == wrongIndex) {
                                randomIndex = -1;
                            }
                        }
                    }
                }
                wrongIndices.push(randomIndex);
            }

            for (let wrongIndex of wrongIndices) {
                this.wrongAnswers.push(this.questions[wrongIndex]);
            }
            window.console.log("new question finished");
            window.console.log(this.answer);
            window.console.log(this.wrongAnswers);
        },
        shuffle(arra1) {
            var ctr = arra1.length, temp, index;

            // While there are elements in the array
            while (ctr > 0) {
                // Pick a random index
                index = Math.floor(Math.random() * ctr);
                // Decrease ctr by 1
                ctr--;
                // And swap the last element with it
                temp = arra1[ctr];
                arra1[ctr] = arra1[index];
                arra1[index] = temp;
            }
            window.console.log(arra1);
            return arra1;
        },
        checkAnswer(answerAttempt) {
            this.answered++;
            if (this.status !== statusTypes.correct) {
                if (answerAttempt == this.answer.name) {
                    this.status = statusTypes.correct;
                    this.answeredCorrect++;
                } else {
                    this.status = statusTypes.wrong;
                }
            }
        },
        nextQuestion() {
            this.questionIndex++;
            this.newQuestion();
            this.status = statusTypes.none;
        }
    }
}
</script>

<style lang="scss" scoped>
img {
    width: 300px;
    border: 5px solid #dfdfdf;
    padding: 10px;
    border-radius: 10px;

    &.correct {
        border-color: green;
    }

    &.wrong {
        border-color: red;
    }
}

ul {
    display: inline-block;
    list-style: none;
    font-family: 'Malgun Gothic', sans-serif;
    width: 350px;
    vertical-align: top;
    margin-top: 0;

    li {
        border: 1px solid #7b65e8;
        padding: 10px;
        border-radius: 10px;
        width: 100%;
        margin-bottom: 10px;
        background-color: #cacaff;

        &:hover {
            cursor: pointer;
            background-color: #a3a3d5;
        }

        @media (max-width: 690px) {
            width: auto;
        }
    }

    @media (max-width: 690px) {
        display: block;
        width: 100%;
        padding-left: 0;
    }
}

button {
    display: block;
    width: 745px;
    padding: 10px;
    border-radius: 10px;
    font-family: 'Malgun Gothic', sans-serif;
    font-size: 18px;
    margin-top: 10px;
    cursor: pointer;
    background-color: #aff06d;
    border: 1px solid #6ca26c;

    &:hover {
        background-color: #98d05f;
    }

    @media (max-width: 690px) {
        width: 100%;
    }
}


</style>